<template>
	<div class="discover">
		<div class="tit">
			<span class="s1">发现</span><span class="s2" @click='selectCity'>{{location?location:"城市"}}<img src="./sc.png" alt=""></span>
		</div>
		<div class="content">
			<div class="ban">
				<div class="swiper-container1">
					<div class="swiper-wrapper">
						<div class="swiper-slide">
							<img src="http://images4.c-ctrip.com/target/fd/tuangou/g1/M00/E0/65/CghzfFUTLmqADCpEAAa7UuG441Q682.jpg" alt="">	
						</div>
						<div class="swiper-slide">
							<img src="http://img.mp.itc.cn/upload/20160617/fd75a29acb25434b99f6ac08ed9edea8_th.jpg" alt="">	
