						<div class="swiper-wrapper">
							<span v-for='(item,index) in hotlocation' :key='index' @click='tab(index)' class="tabs_item swiper-slide" :class="acflag==index?'ac':''">{{item}}<b></b></span>
							<span  class="tabs_item swiper-slide" ></span>
						</div>
					</div>
				</div>
				<div class="re_list">
					<div class="re_li_item" ref='reLiLeft'>
						<div v-if='index%2==0' ref='relibox' class="re_li_box" v-for='(item,index) in recommendList' :key='index'>
							<div class="desimg"><img :src="item.imgUrl" alt=""></div>
							<div class="destxt bold">{{item.des}}</div>
							<div class="tags">
								<span class="s1"><img :src="item.tags.icon" alt="">{{item.tags.neak}}</span>
								<span class="s2"><img src="./thnoac.png" alt="">{{item.tags.mark}}</span>
							</div>
						</div>
					</div>
					<div class="re_li_item">
