							<div class="destxt bold">{{item.des}}</div>
							<div class="tags">
								<span class="s1"><img :src="item.tags.icon" alt="">{{item.tags.neak}}</span>
								<span class="s2"><img src="./thnoac.png" alt="">{{item.tags.mark}}</span>
							</div>
						</div>
					</div>
					
				</div>
				<div class="re_more">{{loadTxt}}</div>
			</div>
		</div>
		<bot :acIndex='3'></bot>
	</div>
