				console.log(this.scrollTop,that.domH)
				that.domH = that.getComputedHeight(that.$refs.relibox)
							console.log(that.domH)
				if(this.scrollTop>200){
					this.sticky = true
					if(this.scrollTop>this.domH-260){
						this.loadTxt = '加载中'
