					}else{
						this.loadTxt = '更多三里屯优质房源'
					}
				}else{
					this.sticky = false
				}
			},
			tab(index){
				this.acflag = index
			},
			selectCity(){
				this.$router.push({name:'citySelect',query:{path:'discover'}})
			},
			getComputedHeight(arr){
				var h=0
				for(var i in arr){
					h+=arr[i].clientHeight
				}
				return h
			}
		},
		mounted(){
			window.addEventListener('scroll', this.handleScroll)
			this.domH = this.getComputedHeight(this.$refs.relibox)
			console.log(this.domH)
			var swiper1 = new Swiper('.swiper-container1', {
		      	slidesPerView: 'auto',
		      	spaceBetween: 15,
		      	centeredSlides: true,
		        effect : 'cube',
			  	// cubeEffect: {
				  //   slideShadows: true,
				  //   shadow: true,
				  //   shadowOffset: 100,
				  //   shadowScale: 0.6
			  	// },
		    });
		    var swiper2 = new Swiper('.swiper-container2', {
		      slidesPerView: 5,
		      spaceBetween: 0,
		      freeMode: true, 
		    });
		},
	}
</script>
<style lang="stylus" scoped>
	.sticky
		position fixed
		left 0 
		top 0.59rem
		width 100%
		background #fff
		height 0.35rem
		padding 0.1rem 0
	.discover
		.tit
			position fixed
			left 0
			top 0
			width 100%
			height 0.6rem
			line-height 0.6rem
			font-size 0.18rem
			text-align left
			box-shadow 0 0 0.05rem #dedede
			z-index 100
			background #fff
			.s1
				padding-left 0.15rem
				font-size 0.24rem
