					text-align left
					height 0.3rem
					line-height 0.3rem
					padding 0 0.15rem
					span
						padding-bottom 0.05rem
						text-align center
						position relative
						b
							transition all ease 0.3s
							position absolute
							display inline-block
							width 0rem
							height 0.02rem
							background #fff
							bottom 0
							transfrom translateX(-30%)
							left 30%
							
					span.ac
						color #ff9645
						font-weight bold
						padding-bottom 0.05rem
						font-size 0.18rem
						b
							background #ff9645
							width 0.3rem
				.re_list
					margin-top 0.2rem
					padding 0 0.15rem
					text-align left
					font-size 0
					display flex
					.re_li_item
						width 48%
						display inline-block
						padding-bottom 0.1rem
						flex 1
						.re_li_box
							margin-bottom 0.1rem
						.desimg
							img
								width 100%
								border-radius 0.10rem
						.destxt
							font-size 0.16rem
							line-height 0.2rem
							margin-top 0.1rem
						.tags
							font-size 0.14rem
							color #999
							padding 0.1rem 0
							height 0.3rem
							line-height 0.3rem
							span.s1
								padding-right 0.05rem
