									width 0.3rem
									height 0.3rem
									border-radius 0.3rem
									margin-right 0.05rem
									vertical-align top
							span.s2
								float right
								img
									margin-right 0.05rem
									vertical-align top
						.price
							font-size 0.16rem
							.new
								font-size 0.18rem
								margin-right 0.05rem
							.old
								text-decoration line-through
					.re_li_item:nth-child(2n+1)
						padding-right 2%
					.re_li_item:nth-child(2n)
						padding-left 2%
				.re_more
					color #333
					font-size 0.16rem
					width 2rem
					height 0.4rem
					line-height 0.4rem
					background #F4F5F7
					text-align center
					border-radius 1rem
					margin 0.3rem auto
					margin-top 0
					margin-bottom 1rem
					

			



</style>


